<template>
    <div id="blog" class="d-flex flex-column justify-start align-center">
        <v-container>
            <div class="my-10 d-flex flex-column justify-center align-center">
                <p class="blog-title scroll-out-item-top">
                    our recent blog
                </p>
                <v-img
                    :src="require('../../assets/chat-app/line.png')"
                    class="my-2"
                    contain
                    width="100"/>
                <v-row class="d-flex justify-center">
                    <v-col cols="10" md="4"
                        v-for="(item, i) in blogData"
                        :key="i"
                        class="blog-box scroll-out-item-top-sp">
                        <v-card
                            :loading="item.loading"
                            class="blog-card mx-auto my-4 my-sm-12 pb-4"
                            max-width="374">
                            <template slot="progress">
                                <v-progress-linear
                                    color="deep-purple"
                                    height="10"
                                    indeterminate></v-progress-linear>
                            </template>

                            <div class="blog-box-img">
                                <v-img
                                    class="blog-img"
                                    height="250"
                                    :src="require('../../assets/chat-app/'+item.img)"></v-img>
                            </div>

                            <v-card-title>
                                <span class="blog-content-title">{{ item.title }}</span>
                            </v-card-title>

                            <v-card-subtitle class="">
                                {{ item.postBy }}
                            </v-card-subtitle>

                            <v-card-text>
                                <span class="blog-content">
                                    {{ item.content }}
                                </span>
                            </v-card-text>

                            <v-card-actions>
                                <v-btn
                                    color="deep-purple lighten-2"
                                    outlined
                                    @click="reserve(i)">
                                    view more
                                </v-btn>
                            </v-card-actions>
                        </v-card>
    
                    </v-col>
                </v-row>
            </div>
        </v-container>
    </div>
</template>

<script>
import ScrollOut from "scroll-out";
import { mapGetters } from "vuex";
    export default {
        name: 'Blog',

        mounted() {
            this.so = ScrollOut({
                scope: this.$el,
                once: this.getScrollOutOnce,
                threshold: 0.5,
                targets:
                    ".scroll-out-item-top, .scroll-out-item-top-sp",
            });
        },
        
        destroyed() {
            this.so.teardown();
        },

        computed: {
            ...mapGetters({
                getScrollOutOnce: "getScrollOutOnce",
            }),
        },

        data: () => ({
            blogData: [
                {
                    title: 'Cafe Badilico',
                    postBy: 'Jhon Doe | 2 Oct | 25 Comments | 30 View ',
                    img: 'blog-1.jpg',
                    content: 'Small plates, salads & sandwiches - an intimate setting with 12 indoor seats plus patio seating.',
                    loading: false,
                },
                {
                    title: 'Cafe Alexa',
                    postBy: 'Jhon Doe | 3 Oct | 12 Comments | 17 View ',
                    img: 'blog-2.jpg',
                    content: 'Small plates, salads & sandwiches - an intimate setting with 12 indoor seats plus patio seating.',
                    loading: false,
                },
                {
                    title: 'Cafe Box Game',
                    postBy: 'Jhon Doe | 5 Oct | 7 Comments | 10 View ',
                    img: 'blog-3.jpg',
                    content: 'Small plates, salads & sandwiches - an intimate setting with 12 indoor seats plus patio seating.',
                    loading: false,
                },
            ],
        }),

        methods: {
            reserve (i) {
                this.blogData[i].loading = true
                setTimeout(() => (this.blogData[i].loading = false), 2000)
            },
        },
    }
</script>

<style scoped>
    #blog {
        font-family: 'Poppins', sans-serif;
        min-height: 100vh;
    }
    .blog-box:hover {
        cursor: pointer;
    }
    .blog-box:hover .blog-img {
        transform: rotate(15deg) scale(1.5);
    }
    .blog-box-img {
        overflow: hidden;
    }
    .blog-img {
        transition: .5s;
    }
    .blog-title {
        font-size: 2.2em;
        font-weight: 600;
        color: #6e51e6;
        text-transform: capitalize;
    }
    .blog-content-title {
        color: #6e51e6;
    }
    .blog-content{
        color: #5d5f86;
    }
</style>